import React, { ReactElement, useContext } from "react";
import {
  Alert,
  Button,
  Container,
  Grid,
  Paper,
  Snackbar,
  Typography,
} from "@mui/material";
import { BASE_API_URL } from "../../util/Constants";
import { AuthContext } from "../../security/AuthContext";
import { useSnackbar } from "../hooks/useSnackbar";

export function AdminPage(): ReactElement {
  const { login } = useContext(AuthContext);
  const {
    snackbarOpen,
    showSnackbar,
    snackbarSeverity,
    snackbarText,
    onSnackbarClose,
  } = useSnackbar();

  let resetDataHandler = async () => {
    try {
      const response = await fetch(`${BASE_API_URL}/admin/data/reset`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${login?.accessToken}`,
        },
      });
      if (response.ok) {
        showSnackbar("Data successfully reset!", "success");
      } else {
        showSnackbar(response.statusText, "error");
      }
    } catch (e) {
      showSnackbar(e?.toString(), "error");
    }
  };
  return (
    <Container maxWidth="sm">
      <Grid>
        <Paper>
          <Typography variant={"h3"}>Data</Typography>
          <Typography>
            Reset ALL data
            <Button
              variant={"outlined"}
              style={{ marginLeft: 12 }}
              onClick={resetDataHandler}
            >
              Reset
            </Button>
            <Snackbar
              open={snackbarOpen}
              onClose={onSnackbarClose}
              autoHideDuration={5_000}
            >
              <Alert severity={snackbarSeverity}>{snackbarText}</Alert>
            </Snackbar>
          </Typography>
        </Paper>
      </Grid>
    </Container>
  );
}
